import React, { Component } from 'react';
import { VictoryLine, VictoryChart, VictoryTheme } from 'victory';
class User extends Component {
    state = {
        "user": undefined,
        "user_history": [{ "time": 0, "total": 0 }]
    }

    componentDidMount() {
        fetch('https://santa-shares.azurewebsites.net/api/users/' + this.props.user_id)
            .then(res => res.json())
            .then(data => {
                let user = data
                user.stock_value = user.items.reduce((total, item) => total += item.price * item.amount, 0)
                user.total_value = user.stock_value + user.balance
                if (user.items === undefined) user.items = []
                for (let i = 0; i < user.items.length; i++) {
                    let item = user.items[i];
                    item.sub_total = item.amount * item.price
                }
                this.setState({ user: user })
                console.log(this.state)

            })
            .catch(console.log)

        fetch('https://santa-shares.azurewebsites.net/api/users/' + this.props.user_id + '/history')
            .then(res => res.json())
            .then(data => {
                let user_history = []
                for (let i = 0; i < data.length; i++) {
                    let d = data[i];
                    user_history.push({ "time": new Date(d.time), "total": d.balance + d.stock_value })
                }
                this.setState({ user_history: user_history })
                console.log(this.state)
            })
            .catch(console.log)
    }

    render() {
        if (this.state.user === undefined) return (
            <div class="container">
                <h5>Loading user data...</h5>
                <div class="progress">
                    <div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style={{ width: "50%" }}></div>                </div>
            </div>
        )
        else return (
            <div>
                <div class="container">
                    <h3>{this.state.user.user_name}</h3>
                    <h5>Summary</h5>
                    <table class="table table-hover">
                        <tbody>
                            <tr>
                                <th scope="row">Cash</th>
                                <td style={{ textAlign: 'center' }}>{"£" + ((this.state.user.balance / 100).toFixed(2))}</td>
                            </tr>
                            <tr>
                                <th scope="row">Stock Value</th>
                                <td style={{ textAlign: 'center' }}>{"£" + ((this.state.user.stock_value / 100).toFixed(2))}</td>
                            </tr>
                            <tr>
                                <th scope="row">Total</th>
                                <td style={{ textAlign: 'center' }}>{"£" + ((this.state.user.total_value / 100).toFixed(2))}</td>
                            </tr>
                        </tbody>
                    </table>
                    <h5>Items</h5>
                    <table class="table table-hover">
                        <thead>
                            <tr>
                                <th scope="col">ID</th>
                                <th scope="col">Name</th>
                                <th style={{ textAlign: 'center' }} scope="col">Price</th>
                                <th style={{ textAlign: 'center' }} scope="col">Amount</th>
                                <th style={{ textAlign: 'center' }} scope="col">SubTotal</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.user.items.map((item) => (
                                    <tr key={item.item_id}>
                                        <td>{item.item_id}</td>
                                        <td>{item.item_name}</td>
                                        <td style={{ textAlign: 'center' }}>{"£" + ((item.price / 100).toFixed(2))}</td>
                                        <td style={{ textAlign: 'center' }}>{item.amount}</td>
                                        <td style={{ textAlign: 'center' }}>{"£" + ((item.sub_total / 100).toFixed(2))}</td>
                                    </tr>
                                ))}
                        </tbody>
                    </table>
                </div>
                <div class="container">
                    <VictoryChart theme={VictoryTheme.material} style={{width: "50%" }}>
                        <VictoryLine
                            style={{
                                data: { stroke: "#c43a31" },
                                parent: { border: "1px solid #ccc" }
                            }}
                            data={this.state.user_history} x="time" y="total" />
                    </VictoryChart>
                </div>
            </div>
        )
    }
}




export default User;